	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Ellipse</h2>\n<p>A Circle is stretched by factor <strong>r</strong>(radius) in all directions.</p>\n<p>Hence its area is caluculated by formula A= pi *r<sup>2</sup> where pi is a mathematical constant, pi = 22/7 = 3.141592…</p>\n<p>An Ellipse is just like a Circle, but is stretched by a factor <strong>a</strong> in the x-direction, and a factor <strong>b</strong> in the y-direction.\n<strong>a</strong> is semi-major axis length and <strong>b</strong> is semi-minor axis length.</p>\n<p>Hence Ellipse area is calculated by formula A = pi *a *b</p>\n<p><img src=\"http://convertxy.com/media/images/ellipse.gif\" alt=\"alt\"></p>\n<h4>More Information:</h4>\n<p>For more details visit <a href=\"https://en.wikipedia.org/wiki/Ellipse\">wiki</a></p>","fields":{"slug":"/mathematics/area-of-a-ellipse/"},"frontmatter":{"title":"Area of a Ellipse"}}},"pathContext":{"slug":"/mathematics/area-of-a-ellipse/"}}

